@mixin onPhone {
  @media (min-width: 320px) {
    @content;
  }
}

@mixin width {
  @media (max-width: 1020px) {
    @content;
  }
}

@mixin onTablets {
  @media (min-width: 640px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onFullDesktop {
  @media (min-width: 1200px) {
    @content;
  }
}
